<template>
  <div class="list-group">
    <a class="list-group-item list-group-item-heading list-item-head">模块管理</a>
    <ul>
      <li v-for="(menu,index) in menus" :key="index">
        <button @click="changeMenu(index,menu,$event)" v-if="menu.pageInf.type === 0" :class="['list-group-item btn btn-link menu-list',{active:menu.pageInf.show}]">
          <span>{{menu.pageInf.pagename}}</span>
        </button>
        <transition mode="out-in">
        <ul v-show="menu.pageInf.show">
          <li v-for="list of menu.list" :key="list.pageInf.id">
           <router-link class="ul-a" active-class="active" :to="'/main/'+list.pageInf.url" v-text="list.pageInf.pagename"></router-link>
          </li>
        </ul>
        </transition>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data: function() {
    return {};
  },
  mounted: function() {
    var vm = this;
    vm.loginmenu(vm);
  },
  computed: {
    menus: {
      get() {
        return this.$store.getters.GETUSERPAGE;
      },
      set(data) {
        this.menus = data;
      }
    }
  },
  methods: {
    loginmenu: function(vm) {
      var userpage = vm.$store.getters.GETUSERPAGE;
      console.log(userpage)
      if (userpage==null||userpage.length==0) {
        vm.$api.http.loginleftmenu(this);
      }
    },
    changeMenu(index, item, e) {
      //  var event=e.target.nextSibling.nextSibling;
      this.menus.forEach(i => {
        // 判断如果数据中的headerData[i]的show属性不等于当前数据的show属性那么headerData[i]等于false
        if (i.pageInf.show !== this.menus[index].pageInf.show) {
          i.pageInf.show = false;
        }
      });
      item.pageInf.show = !item.pageInf.show;
    }
  }
};
</script>
<style scoped>
.active {
  z-index: 2;
  color: #fff;
  background-color: #337ab7 !important;
  border-color: #f1f3f5;
}
.menu-list:hover,
.menu-list.active {
  background: #337ab7 !important;
}
ul {
  list-style-type: none;
  padding: 0;
}

.ul-a {
  height: 3rem;
  line-height: 3rem;
  width: 100%;
  display: block;
  text-decoration: none;
  text-align: center;
  background: rgb(247, 247, 247);
  color: #000;
  border: rgba(0, 0, 0, 0.096) solid 0.002rem;
  border-radius: 0.5rem;
}
.ul-a:hover {
  color: rgb(255, 255, 255) !important;
  background: #5050499a !important;
}
.v-enter-active {
  animation: v-in .5s;
}
.v-leave-active {
  animation: v-out .2s ;
}
@keyframes v-in {
  0% {
    height: 0;
    opacity: 0;
  }
  50% {
    height: 50%;
  }
  100% {
    height: 100%;
    opacity: 1;
  }
}
@keyframes v-out {
  0% {
    height: 100%;
    opacity: 1;
  }
  50% {
    height: 50%;
  }
  100% {
    height: 0;
    opacity: 0;
  }
}
</style>



